<template>
<div class="main">
            <div class="module contacts">
                <div class="module-center">
                    <div class="module-line contacts">
                        <img class="image" src="@/assets/svg/contacts_pic.svg">
                    </div>
                    <h2 class="module-title"> </h2>
                    <div class="module-text">
                        <i>  . .   </i>
                    </div>
                    <div class="module-content">
                        <div class="box primary contacts">
                            <form class="form">
                                <label class="form-field">
                                    <input v-model="name" class="field input" type="text" required/>
                                    <span class="name" >*</span>
                                    <span class="error">  </span>
                                </label>
                                <label class="form-field">
                                    <input v-model="mail" name='email' class="field input" type="text" required/>
                                    <span class="name">*. </span>
                                    <span class="error">  </span>
                                </label>
                                <label class="form-field">
                                    <textarea v-model="text" class="field text" rows="10" required></textarea>
                                    <span class="name">*</span>
                                    <span class="error active">  </span>
                                </label>
                                <label class="form-field checkbox">
                                    <input v-model="checked" class="field" type="checkbox" required/>
                                    <span class="icons">
                                        <img class="icon one" src="@/assets/svg/checkbox.svg"/>
                                        <img class="icon two" src="@/assets/svg/checkbox_on.svg"/>
                                    </span>
                                    <span class="text">
                                         
                                        &nbsp;<a href="/policy">   </a>
                                    </span>
                                    <span class="error">  </span>
                                </label>
                                <div class="buttons">
                                    <button class="button primary yellow"  :disabled ="disabled" @click="send"></button>
                                    <div class="error ">.    .</div>
                                </div>
                            </form>
                            <div class="message">
                                ,  
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
export default {
  name: 'ContactsView',
  props: {
    msg: String
  },
    data () {
        return {
            name: 'Alex',
            mail: 'x@y.com',
            text: 'test',
            checked: false,
            psk:'0xEA1B20D8FF1C45BA',
            disabled: true,

        }
    },
    watch:{
        name(old, yang){
            this.validation()  
        },
        mail(old, yang){
            this.validation()  
        },
        checked(old, yang){
            this.validation()  
        },

    },
    methods:{
        validation(){
            if((this.name.length != 0) && (this.mail.length != 0) && this.checked ) this.disabled = false; else this.disabled = true
 
        },
        send(){
            fetch('php/smail.php', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(
                    [
                        { name:"name", value: this.name },
                        { name:"mail", value: this.mail },
                        { name:"text", value: this.text },
                        { name:"psk",  value: this.psk  }
                    ]
                )
            })
            .then(res => res.json())
            .then(data => {
                console.log(data)
                if(data.status =='OK') alert(' ')
                if(data.status =='OPSKFAIL') alert('  ')
                if(data.status =='FAIL') alert('   ')
            })
            .catch(error => console.log(error));
        }
    }
}
</script>

